import React from "react";
import { Grid } from "@material-ui/core";

// styles
import useStyles from "./styles";

// components
import PageTitle from "../../components/PageTitle";
import Widget from "../../components/Widget";
import { Typography } from "../../components/Wrappers";

export default function TypographyPage() {
  var classes = useStyles();

  return (
    <>
      <PageTitle title="" />
      <Grid container spacing={4}>
        <Grid item xs={12} md={6}>
          <Widget title=" " disableWidgetMenu>
            <div className={classes.dashedBorder}>
              <Typography variant="h1" className={classes.text}>
                h1.  
              </Typography>
              <Typography variant="h2" className={classes.text}>
                h2.  
              </Typography>
              <Typography variant="h3" className={classes.text}>
                h3.  
              </Typography>
              <Typography variant="h4" className={classes.text}>
                h4.  
              </Typography>
              <Typography variant="h5" className={classes.text}>
                h5.  
              </Typography>
              <Typography variant="h6">h6.  </Typography>
            </div>
          </Widget>
        </Grid>
        <Grid item xs={12} md={6}>
          <Widget title=" " disableWidgetMenu>
            <div className={classes.dashedBorder}>
              <Typography variant="h1" color="primary" className={classes.text}>
                primary  
              </Typography>
              <Typography variant="h2" color="success" className={classes.text}>
                success  
              </Typography>
              <Typography
                variant="h3"
                color="secondary"
                className={classes.text}
              >
                secondary  
              </Typography>
              <Typography variant="h4" color="warning" className={classes.text}>
                warning  
              </Typography>
              <Typography
                variant="h5"
                color="primary"
                colorBrightness="light"
                className={classes.text}
              >
                primary light  
              </Typography>
              <Typography variant="h6" color="info">
                info  
              </Typography>
            </div>
          </Widget>
        </Grid>
        <Grid item xs={12} md={6}>
          <Widget title="  " disableWidgetMenu>
            <div className={classes.dashedBorder}>
              <Typography className={classes.text}>  </Typography>
              <Typography className={classes.text} weight="light">
                weight light  
              </Typography>
              <Typography className={classes.text} weight="medium">
                weight medium  
              </Typography>
              <Typography className={classes.text} weight="bold">
                weight bold  
              </Typography>
              <Typography className={classes.text}>
                BASIC UPPERCASE TEXT
              </Typography>
              <Typography className={classes.text}>
                basic lowercase text
              </Typography>
              <Typography className={classes.text}>
                Basic Capitalized Text
              </Typography>
              <Typography>
                <i>Basic Cursive Text</i>
              </Typography>
            </div>
          </Widget>
        </Grid>
        <Grid item xs={12} md={6}>
          <Widget title=" " disableWidgetMenu>
            <div className={classes.dashedBorder}>
              <Typography className={classes.text} size="sm">
                  Typography SM
              </Typography>
              <Typography className={classes.text}>
                  Typography Regular
              </Typography>
              <Typography className={classes.text} size="md">
                  Typography MD
              </Typography>
              <Typography className={classes.text} size="xl">
                  Typography XL
              </Typography>
              <Typography className={classes.text} size="xxl">
                  Typography XXL
              </Typography>
            </div>
          </Widget>
        </Grid>
      </Grid>
    </>
  );
}
